import React from 'react';
import EventsClient from './events-client';
import { getEvents } from '@/actions/event-actions';
import db from '@/lib/db'; // Direct DB access for lists is fine in Server Components

// Helper to fetch students for the dropdown
async function getStudents() {
  try {
    // Return all students formatted for the UI
    const students = db.prepare('SELECT studentId, cne, firstName as prenom, lastName as nom, email FROM students').all();
    return students;
  } catch (error) {
    return [];
  }
}

export default async function EventsPage() {
  // 1. Fetch Real Events
  const events = await getEvents();
  
  // 2. Fetch Real Students (for the "Add Student" dialog)
  const students = await getStudents();

  // 3. Pass data to the Client Component
  return (
    <EventsClient 
      initialEvents={events} 
      initialStudents={students} 
    />
  );
}